<template>
    <div 
        class="moviebox"
         v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
    >
        <MovieItem
            v-for="movie in movies"
            :key="movie.filmId"
            :movie="movie"
        ></MovieItem>
    </div>
</template>

<script>
import MovieItem from "./MovieItem"
import {Toast} from 'mint-ui';
export default {
    props:["type"],
    watch:{
        type(val){
            this.movies = [];
            this.limit = 3;
            this.page = 1;
            this.hasMore = true
            this.getMovies()
        }
    },
    data(){
        return{
            movies:[],
            limit:20,
            page:1,
            loading:false, //默认无限滚动触发
            hasMore:true 
        }
    },
    components:{
        MovieItem
    },
    methods:{
        loadMore(){
            if(!this.hasMore){
                Toast({
                    message: '没有数据了',
                    position: 'bottom',
                });
                this.loading = true
                return false
            }
            
            this.getMovies()
        },
        getMovies(){
            let instance = Toast({
                message: '数据加载中',
                iconClass:"fa fa-refresh",
                duration: -1
            });
            let {limit,page} = this;
            this.loading = true; //请求时关闭无限滚动
            this.$http.get("./api/mz/movie/"+this.type,{
                params:{
                    limit,
                    page
                }
            }).then(res=>{
                /* console.log(this) */
                this.movies = this.movies.concat(res.data.data.object_list)
                this.loading = false //加载完数据后开启无限滚动
                instance.close();//加载完数据后消失
                if(this.limit*this.page >= res.data.data.total){
                    this.hasMore = false //没有数据了
                    return false
                }
                this.page++
            })
        }
    },
    activated(){
        this.loading = false //进入时开启无限滚动
    },
    deactivated(){
        this.loading = true //离开时关闭无限滚动
    }
}
</script>

<style lang="scss">
    .moviebox{
        padding-bottom:60px;
    }
</style>