<template>
    <div class="home-banner" :class="{loading:banners.length===0}">
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div 
                    class="swiper-slide"
                    v-for="banner in banners" 
                    :key="banner.bannerId"
                >
                    <img width="100%" height="210px;" :src="banner.imgUrl" alt="">
                </div>
            </div>
            <!-- 分页器 -->
            <div class="swiper-pagination"></div>   
        </div> 
    </div>
</template>

<script>
import Swiper from "swiper"
export default {
    data(){
        return {
            banners:[]
        }
    },
    methods:{

    },
    created(){
        this.$http.get("/api/mz/movie/banners",{
            params:{
                limit:3
            }
        }).then(res=>{
            /* console.log(res.data.data) */
            this.banners = res.data.data.object_list
            this.$nextTick(()=>{
                new Swiper(".swiper-container",{
                    loop:true,
                    pagination:{
                        el:".swiper-pagination"
                    }
                })
            })
            
        })
    }
}
</script>

<style lang="scss">
    .home-banner{
        position:relative;
        .swiper-container{

            .swiper-pagination{
                position:absolute;
                bottom:10px;
                left:auto;
                right:12px;
                width:auto;
                .swiper-pagination-bullet-active{
                    background: rgba(0, 0, 0, 0.897);
                }
            }
        }
    }
</style>