<template>
    <div class="home">
        <div :class="{fixheader:fixFlag}" class="header" v-if="fixFlag"> <p>青岛 <i class="fa fa-sort-desc"></i> </p>   电影</div>
       <Banner></Banner>
       <div class="navbar" :class="{fixtop:fixFlag}">
            <span
                v-for="nav in navs"
                :key="nav.id"
                :class="{active:type==nav.type}"
                @click="type=nav.type"
            >{{nav.title}}</span>
       </div>
       <div :class="{fixbox:fixFlag}">
           <MovieBox :type="type"></MovieBox>
       </div>
       
       <Tabbar></Tabbar>
    </div>
</template>

<script>
import Tabbar from "@/components/Tabbar"
import Banner from "@/components/Banner"
import MovieBox from "./MovieBox"
export default {
    name:"home",
    data(){
        return {
            type:"specials",
            fixFlag:false,
            navs:[
                {id:1,title:"正在热映",type:"specials"},
                {id:2,title:"即将上映",type:"actives"}
            ]
        }
    },
    components:{
        Tabbar,Banner,MovieBox
    },
    methods:{
        scrollHandler(){
            let sTop = document.documentElement.scrollTop || document.body.scrollTop;
            //  console.log(sTop) 
            if(sTop >= 210 && !this.fixFlag){
                this.fixFlag = true
            }else if(sTop < 210 && this.fixFlag){
                this.fixFlag = false
            }
        }
    },
    activated(){
        window.addEventListener("scroll",this.scrollHandler)
        window.scrollTo(0,this.homeTop)
    },
    deactivated(){
        window.removeEventListener("scroll",this.scrollHandler)
        this.fixFlag = false
    },
    //离开时记录滚动高度
    beforeRouteLeave(to,form,next){
        this.homeTop = document.documentElement.scrollTop || document.body.scrollTop;
        next()
    }
}
</script>


